}

.search-section {
  height: auto;
}

.search-section .search-container {
  max-width: 280px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.search-section .search-container .searchbar {
  flex-direction: row;
  align-items: center;
  display: flex;
}

.search-section .search-container .searchbar input {
  width: 100%;
  height: 50px;
  font: var(--lato400-s14-h17-normal);
  border: 1px solid #008cff;
  border-radius: 25px;
  outline: none;
  margin-top: 36px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 56px;
}

.search-section .search-container .searchbar input:-ms-placeholder-shown {
  font: var(--lato400-s14-h17-normal);
  padding-left: 56px;
}

.search-section .search-container .searchbar input:placeholder-shown {
  font: var(--lato400-s14-h17-normal);
  padding-left: 56px;
}

.search-section .search-container .searchbar .icons {
  fill: rgba(128, 128, 128, .824);
  cursor: pointer;
  margin-top: 35px;
}

.search-section .search-container .searchbar #gps, .search-section .search-container .searchbar #star {
  position: absolute;
}

.search-section .search-container .searchbar #gps {
  left: 20px;
}

.search-section .search-container .searchbar #star {
  right: 20px;
}

.search-section .search-container .cities-scroll {
  flex-direction: row;
  justify-content: space-between;
  padding-top: 15px;
  display: flex;
}

.search-section .search-container .cities-scroll .other-cities {
  color: gray;
  height: 30px;
  width: 30px;
  visibility: hidden;
  border-width: 0;
  border-radius: 20px;
  font-weight: 600;
}

.search-section .search-container .cities-scroll div {
  width: 210px;
  overflow: hidden;
}

.search-section .search-container .cities-scroll div .saved-cities {
  float: left;
  white-space: nowrap;
  margin: 0;
  list-style: none;
  transition-duration: .3s;
  transition-timing-function: linear;
  transform: translateX(0);
}

