.search-section .search-container .searchbar .icons:hover, .search-section .search-container .cities-scroll .other-cities:hover, .search-section .search-container .cities-scroll .city:hover {
  color: gold;
  fill: gold;
  cursor: pointer;
}

.search-section .search-container .cities-scroll div .saved-cities .saved-city .close-city:hover {
  fill: gold;
  width: 12px;
  height: 12px;
}

@media screen and (min-width: 300px) {
  .search-section {
    margin-top: 40px;
    margin-bottom: 50px;
  }
}

@media screen and (min-width: 780px) {
  .search-section {
    margin-top: 10px;
