:root {
  --blackMain: #000;
  --whiteMain: #fff;
  --orangeMain: #ff6b09;
  --blueChart: #0906eb;
  --yellowChart: #ea9a05;
  --greenChart: #067806;
  --gackgroundcolorfivedays: #102136;
  --moreinfoanddash: rgba(255, 255, 255, .3);
  --fivedaysandtemperaturecolor: rgba(255, 255, 255, .54);
  --font-family: "Lato", sans-serif;
}

.fivedayscontainer {
  width: 100%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  transition-duration: 1s;
  transition-timing-function: linear;
  display: none;
}

.wheather-fivedays-section {
  background-color: var(--gackgroundcolorfivedays);
  opacity: .8;
  border-radius: 35px;
  flex-direction: column;
  align-items: center;
  padding: 20px 5px;
  display: flex;
  overflow-x: hidden;
  box-shadow: 0 8px 43px rgba(16, 33, 54, .59);
}

.wheather-fivedays-btns {
  justify-content: center;
  gap: 20px;
  padding: 1% 0;
  display: flex;
}

.wheather-fivedays-btns button {
  width: 27px;
  height: 27px;
  cursor: pointer;
  background-color: rgba(255, 255, 255, .9);
  background-position: 50%;
  background-repeat: no-repeat;
  border: none;
  border-radius: 50%;
  outline: none;
}

.wheather-fivedays-btns button:hover {
  color: #fff;
  background-color: rgba(255, 255, 255, .5);
}

.change-today {
  text-transform: uppercase;
  font: var(--lato700-s14-h17-normal);
  text-align: center;
  background-color: #fff;
  border: 1px solid #008cff;
  border-radius: 20px;
}

.wheather-today {
  width: 280px;
  flex-direction: row;
  justify-content: space-between;
  display: flex;
}

.fivedays-city-country-name {
  color: rgba(0, 0, 0, .725);
  font-family: var(--font-family);
  letter-spacing: 1px;
  text-align: center;
  width: 200px;
  margin-bottom: 20px;
  margin-right: 15px;
  font-size: 18px;
  font-weight: 700;
  line-height: 22px;
  display: inline-block;
}

.weather-fivedays-container {
  width: 270px;
  justify-content: space-around;
  align-items: center;
  gap: 5px;
  margin: 0;
  padding: 0 0 0 180px;
  list-style-type: none;
  transition-duration: .3s;
  transition-timing-function: linear;
  display: flex;
  transform: translateX(0);
}

.weatherfivedays-card {
  width: 70px;
  cursor: pointer;
  text-align: center;
  border-radius: 30px;
  flex-direction: column;
  margin-left: 10px;
  margin-right: 10px;
  display: flex;
}

.weatherfivedays-card:hover, .weatherfivedays-card:focus {
  background-color: rgba(255, 255, 255, .12);
}

.weatherfivedays-card .day {
  color: var(--fivedaysandtemperaturecolor);
  margin: 0;
  font-family: var(--font-family);
  letter-spacing: 1px;
  text-align: center;
  margin-bottom: 15px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.weatherfivedays-card .date {
  letter-spacing: 1px;
  color: var(--whiteMain);
  font-family: var(--font-family);
  text-align: center;
  margin: 0;
  font-size: 16px;
  font-weight: 400;
  line-height: 19px;
}

.weatherfivedays-card .min-max-temp {
  letter-spacing: 1px;
  justify-content: center;
  margin-bottom: 10px;
  display: flex;
}

.fivedays-icon {
  width: 60px;
}

.weatherfivedays-card .min-max-temp .dash {
  color: var(--moreinfoanddash);
  opacity: .2;
  height: 50px;
  border: 1px solid;
  margin-left: 5px;
  margin-right: 5px;
}

.weatherfivedays-card .temp-title {
  color: #767d85;
  font-family: var(--font-family);
  letter-spacing: 1px;
  text-align: center;
  margin: 0 0 10px;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.weatherfivedays-card .min-max-temp .temperature {
  font-family: var(--font-family);
  letter-spacing: 1px;
  text-align: center;
  color: var(--orangeMain);
  margin: 0;
  font-size: 14px;
  font-weight: 400;
